<template>
    <BaseModal
        :name="props.name"
        :mobileMode="true"
        :closeOutside="true"
        :isLoading="!data.ready"
    >
        <template v-slot:content v-if="data.ready">
            <draggable
                v-if="player.panes.default?._queue?.items?.length"
                v-model="player.panes.default._queue.items"
                :item-key="(item) => item.id || item.localId"
                tag="ul"
                handle=".sort-button"
                ghost-class="dragging"
                @end="refreshSlider"
                class="items"
                :scroll="false"

            >
                <template #item="{ element: item }">
                    <li :class="{ opened: player.panes.default?._item?.id === item.id }">
                        <SwipeAction 
                            :buttons="[
                                {
                                    code: 'delete',
                                    name: t('modals.tracks.actions.remove'),
                                    icon: 'close',
                                    color: 'var(--color--option-5)'
                                },
                                {
                                    code: 'track-info',
                                    name: t('modals.tracks.actions.about'),
                                    icon: 'help',
                                    color: 'var(--color--option-2)'
                                }
                            ]"
                            @change="itemAction(item, $event)"
                        >
                            <div class="track-box">
                                <TrackPreview :item="item"
                                    :light="true"
                                />

                                <Button icon="drag"
                                    @touchstart.stop=""
                                    @touchmove.stop=""
                                    class="tertiary submenu size-m sort-button" />
                            </div>
                        </SwipeAction>

                    </li>
                </template>
            </draggable>

            <EmptyLabel
                v-else
                class="empty"
                :title="t(`modals.tracks.empty.title`)"
                :description="t(`modals.tracks.empty.description`)"
            />

            <div class="footer">
                <IconButton icon="random"
                    class="tertiary size-l"
                    @click="shuffle"
                />
            </div>
        </template>
        <!-- <template v-slot:footer v-if="data.ready"> -->
            
           <!--  <KebabMenu
                icon="plus"
                v-if="true"
                :leftAuto="locale.rtl"
                :rightAuto="!locale.rtl"
                :tertiary="true"
                :invert="true"
                size="size-m"
                @toggle="data.addKebabActive = !data.addKebabActive"
            >
                <template v-slot:trigger>
                    <IconButton 
                        icon="plus"
                        :class="{
                            active: data.addKebabActive,
                            invert: true,
                            tertiary: true,
                            'size-s': true
                        }"
                    />
                </template>
                <template v-slot>
                    <ul>
                        <li @click="selectToBeginning">
                            <Icon icon="plus" />
                            <span>{{ t('modals.playlist.add.beginning') }}</span>
                        </li>
                        <li @click="selectToEnd">
                            <Icon icon="plus" />
                            <span>{{ t('modals.playlist.add.end') }}</span>
                        </li>
                    </ul>
                </template>
            </KebabMenu> -->

            <!-- <IconButton icon="plus" class="tertiary invert size-s" @click="selectTracks" /> -->
        <!-- </template> -->
    </BaseModal>
</template>

<script setup>
import { onMounted, onBeforeUnmount, reactive, computed, nextTick } from 'vue';
import { useI18n } from 'vue-i18n';
import draggable from 'vuedraggable'

import BaseModal from '/components/modals/BaseModal.vue';
import Button from '/components/ui/Button.vue';
import IconButton from '/components/ui/IconButton.vue';
import PlaylistPreview from '/components/ui/PlaylistPreview.vue';
import KebabMenu from '/components/ui/KebabMenu.vue';
import EmptyLabel from '/components/ui/EmptyLabel.vue';
import TrackPreview from '/components/ui/TrackPreview.vue';
import { delay } from '/utils/utils';
import SwipeAction from '/components/ui/SwipeAction.vue';
import Shake from 'shake.js';

import api from '/api';
import stores from '@/stores';

const player = stores.player();
const locale = stores.locale();
const modals = stores.modals();
const others = stores.others();

const { t } = useI18n();

const props = defineProps({
    name: {
        type: String,
        default: 'tracks'
    }
});

const data = reactive({
    ready: false,
    playlist: null,
    rlease: null,
    tracks: [],
    shakeEvent: null
});

const refreshSlider = async () => {
    // if (player.panes.default?._item) {
    //     const clone = player.panes.default?._item;
    //     delete player.panes.default._item;

    //     await nextTick();

    //     player.panes.default._item = clone;
    // }
}

const shuffle = () => {
    player.queue.shuffle();
    refreshSlider();

    others.vibrate('heavy');
}

const close = () => {
    modals.close(props.name);
}

const load = async () => {
    data.ready = true;
}

const itemAction = (item, action) => {
    others.vibrate();

    if (action === 'track-info') {
        modals.open('track-kebab', {
            quietClose: true,
            data: {
                id: item?.id
            }
        });
    }

    if (action === 'delete') {
        setTimeout(() => {
            player.queue.removeTrack(item.id);
        }, 100);
    }
}



const requestMotionPermission = async () => {
    if (
        typeof DeviceMotionEvent !== 'undefined' &&
        typeof DeviceMotionEvent.requestPermission === 'function'
    ) {
        const permission = await DeviceMotionEvent.requestPermission()
        if (permission !== 'granted') {
            alert('    ')
            return false
        }
    }
    return true
}

const startShake = async () => {
    const allowed = await requestMotionPermission()
    if (!allowed) {
        return
    }

    data.shakeEvent = new Shake({
        threshold: 15, // 
        // timeout: 1000
    });

    data.shakeEvent.start()

    window.addEventListener('shake', shuffle, false)
}


onMounted(() => {
    startShake();
    // data.id = modals.getData(props.name);
    load();
});

onBeforeUnmount(() => {
    if (data.shakeEvent) {
        data.shakeEvent.stop()
        window.removeEventListener('shake', shuffle)
    }
});
</script>

<style lang="scss" scoped>
    .items {
        display: flex;
        margin: 0;
        padding: 12px 0 50px;
        list-style: none;
        flex-direction: column;
        min-width: calc(100% + 40px);
        margin: 0 -20px;


        li {
            display: flex;
            min-height: 45px;
            align-items: center;
            cursor: pointer;
            gap: 8px;
            padding: 0;

            .swipe-wrapper {
            }

            .track-box {
                padding: 8px 8px 8px 24px;
                display: flex;
                align-items: center;
                width: 100%;
                gap: 8px;
            }

            & > span {
                font: var(--font-body-1);
                color: var(--dark-500);
            }

            &.active {
                background: var(--light-20);
                & > span {
                    font-weight: bold;
                }
            }

            .sort-button {
                // margin-right: -16px;
                cursor: grab;
                fill: var(--white-500);
                touch-action: none;
            }

            &.opened {
                background: var(--white-80);
            }

            // &:hover {
            //     background: var(--light-10);
            //     & > span {
            //         font-weight: bold;
            //     }
            // }
        }
    }


    .footer {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: start;
        gap: 8px;
        padding: 0 8px 8px;
        padding-bottom: calc(8px + env(safe-area-inset-bottom));
        position: absolute;
        bottom: 0;
        z-index: 1;
        left: 0;
        right: 0;
        background: var(--white-500);

        &:after {
            content: "";
            position: absolute;
            top: -18px;
            left: 0;
            right: 0;
            min-height: 20px;
            background: linear-gradient(0deg, var(--white-500) 0%, transparent 100%);
            display: block;
            z-index: 1;
        }
    }
    .loader {
        margin: auto;
        flex-shrink: 0;
    }

    button {
        flex-shrink: 0;
    }
</style>
