<template>
    <div
        class="swipe-wrapper lock-page-swipe"
        @touchstart.passive="onTouchStart"
        @touchmove="onTouchMove"
        @touchend="onTouchEnd"
        @touchcancel="onTouchEnd"
    >
        <ul
            v-if="props.buttons.length && data.deltaX"
            class="actions"
            :class="{ active: button?.code }"
            :style="{
                'background-color': button?.color ? button.color : 'transparent',
                width: Math.abs(data.deltaX) + 'px'
            }"
        >
            <li v-for="item in props.buttons"
                :key="item.code"
            >
                <Icon v-if="item.icon" :icon="item.icon" />
                <span>{{ item.name }}</span>
            </li>
        </ul>

        <div
            class="swipe-content"
            :style="contentStyle"
        >
            <slot></slot>
        </div>
    </div>
</template>

<script setup>
    import { ref, computed, reactive } from 'vue';
    import stores from '@/stores';

    const emit = defineEmits(['change']);

    const props = defineProps({
        buttons: { type: Array, default: () => [] }
    });

    const locale = stores.locale();

    const data = reactive({
        dragging: false,
        locked: false,
        startX: 0,
        startY: 0,
        deltaX: 0,
        deltaY: 0
    })

    const startX = ref(0);
    const startY = ref(0);
    const deltaX = ref(0);
    const deltaY = ref(0);

    const VERTICAL_LOCK_PX = 10;
    const HORIZONTAL_ACTIVATE_PX = 10;
    const ACTION_TRIGGER_PX = 60;
    const LI_WIDTH = 80;


    const maxOffset = computed(() => props.buttons.length * LI_WIDTH + parseInt(LI_WIDTH / 2)); //  

    const contentStyle = computed(() => {
        const x = data.deltaX;
        return {
            transform: `translateX(${x}px)`,
            transition: data.dragging ? '' : 'transform 0.25s ease',
            touchAction: Math.abs(data.deltaX) > HORIZONTAL_ACTIVATE_PX
                ? 'none'
                : 'pan-y'
        };
    });

    const onTouchStart = (e) => {
        if (!props.buttons.length) {
            return;
        }

        const t = e.touches[0];
        data.startX = t.clientX;
        data.startY = t.clientY;
        data.deltaX = 0;
        data.deltaY = 0;

        data.dragging = true;
        data.locked = false;
    }

    const onTouchMove = (e) => {
        if (!data.dragging || data.locked) {
            return;
        }

        const t = e.touches[0];
        data.deltaX = t.clientX - data.startX;
        data.deltaY = t.clientY - data.startY;

        //     
        if (Math.abs(data.deltaY) > VERTICAL_LOCK_PX &&
            Math.abs(data.deltaY) > Math.abs(data.deltaX)
        ) {
            data.locked = true;
            reset();
            return;
        }

        let effectiveX = data.deltaX;

        if (locale.rtl) {
            effectiveX *= -1;
        }

        //  
        effectiveX = Math.min(
            Math.max(effectiveX, -maxOffset.value),
            maxOffset.value,
            0
        );

        data.deltaX = locale.rtl ? -effectiveX : effectiveX;
    }

    const onTouchEnd = () => {
        if (!data.dragging) {
            return;
        }

        data.dragging = false;

        if (button.value?.code) {
            emitChange(button.value.code)
        }

        reset();
    }

    const button = computed(() => {
        const absX = Math.abs(data.deltaX);
        if (absX >= ACTION_TRIGGER_PX && props.buttons.length) {
            const index = Math.min(
                Math.round(absX / LI_WIDTH),
                props.buttons.length
            );

            return props.buttons[index - 1];
        }
    });

    const reset = () => {
        data.deltaX = 0;
    }

    const emitChange = (code) => {
        emit('change', code);
    }
</script>

<style lang="scss" scoped>
.swipe-wrapper {
    position: relative;
    overflow: hidden;
    flex: 1;

    .actions {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        display: flex;
        flex-direction: row-reverse;
        justify-content: flex-start;
        transition: background .4s ease, opacity .4s ease;
        list-style: none;
        margin: 0;
        padding: 0;
        overflow: hidden;
        opacity: .4;

        background-image:
            repeating-linear-gradient(
              45deg,
              var(--dark-5) 0,
              var(--dark-5) 1px,
              transparent 1px,
              transparent 20px
            ),
            repeating-linear-gradient(
              -45deg,
              var(--dark-5) 0,
              var(--dark-5) 1px,
              transparent 1px,
              transparent 20px
            );

        background-size: 20px 20px;
        background-position: right top;

        & > li {
            width: 80px;
            min-width: 80px;
            border: none;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 4px;

            svg {
                width: 16px;
                height: 16px;
                fill: var(--dark-80);
            }

            & > span {
                font: var(--font-caption);
                color: var(--dark-500);
                text-align: center;
            }
        }

        &.active {
            opacity: 1;
        }
    }
}



.swipe-content {
    position: relative;
    z-index: 1;
    will-change: transform;
    display: flex;
}
.direction-rtl {
    .swipe-wrapper {
        .actions {
            right: auto;
            left: 0;
            background-position: left top;
        }
    }
}
</style>
